import {mapGetters, mapState} from "vuex";
import appSpecialTopicList from "../../components/page-component/app-special-topic/app-special-topic-list.vue";

let page = 1;
let search = {};
let is_no_more = false;
let is_loading = false;
export default {
    name: "list",
    components: {
        appSpecialTopicList,
    },
    data() {
        return {
            list: [],
            typeList: [],
            tabCurrentIndex: 0,
            KEY: '_TOPIC_SEARCH_DATA',
            historyList: [],
            keyword: '',
            isSearch: false,
        };
    },
    computed: {
        ...mapState({
            bar_title: state => state.mallConfig.bar_title,
        }),
        ...mapGetters('mallConfig', {
            getTheme: 'getTheme',
        }),
    },
    watch: {
        keyword: {
            handler(n, o) {
                if (n !== o) {
                    this.isSearch = true;
                }
            },
            immediate: true,
        }
    },
    onLoad(options) { this.$commonLoad.onload(options);
        page = 1;
        is_no_more = false;
        is_loading = false;
        if (typeof options.type != 'undefined') {
            search.type = options.type;
        }
        this.loadData();
        this.loadType();
    },
    onReachBottom() {
        if (is_no_more || this.isSearch) {
            return;
        }
        this.loadData();
    },

    onShareAppMessage() {
        let route = this.$platDiff.route();
        let title = '专题列表';
        for (let i in this.bar_title) {
            if (this.bar_title[i].value == route) {
                title = this.bar_title[i].new_name;
                break;
            }
        }
        return this.$shareAppMessage({
            title: title,
            path: '/pages/topic/list',
        });
    },

    methods: {
        historyClick(keyword) {
            this.keyword = keyword.trim();
            this.searchList();
        },
        setHistory() {
            let historyList = this.getHistory();
            let keyword = this.keyword.trim();

            if (!keyword) {
                return;
            }
            historyList.forEach((item, index) => {
                if (item.keyword === keyword) historyList.splice(index, 1);
            });
            historyList.unshift({
                keyword: keyword,
            });
            if (historyList.length > 20) historyList.pop();
            this.historyList = historyList;
            this.$storage.setStorageSync(this.KEY, historyList);
        },
        getHistory() {
            let historyList = this.$storage.getStorageSync(this.KEY);
            if (!historyList) historyList = [];
            return historyList;
        },
        deleteHistory() {
            let self = this;
            uni.showModal({
                content: '确认删除全部历史记录？',
                success: function (res) {
                    if (res.confirm) {
                        self.$storage.removeStorageSync(self.KEY);
                        self.historyList = [];
                    }
                }
            });
        },
        clear() {
            uni.hideKeyboard()
            this.keyword = '';
        },
        searchList() {
            page = 1;
            this.keyword = this.keyword.trim();
            this.loadData();
        },
        loadData() {
            if (is_loading) {
                return;
            }
            this.$showLoading();
            is_loading = true;
            search.page = page;
            this.setHistory();
            this.$request({
                url: this.$api.topic.list,
                data: Object.assign({}, {keyword: this.keyword}, search)
            }).then(response => {
                is_loading = false;
                this.isSearch = false;
                this.historyList = this.getHistory();
                this.$hideLoading();
                if (response.code === 0) {
                    if (page === 1) {
                        this.list = [];
                    }
                    if (response.data.list.length > 0) {
                        this.list = this.list.concat(response.data.list);
                        page++;
                    } else {
                        is_no_more = true;
                    }
                } else {

                }
            }).catch(e => {
                this.$hideLoading();
                is_loading = false;
            });
        },
        loadType() {
            this.$request({
                url: this.$api.topic.type,
            }).then(response => {
                if (response.code === 0) {
                    if (response.data.list.length > 0) {
                        let list = [
                            {
                                id: 0,
                                name: '全部'
                            },
                            {
                                id: -1,
                                name: '精选'
                            }
                        ];
                        this.typeList = list.concat(response.data.list);
                        for (let i in this.typeList) {
                            if (this.typeList[i].id == search.type) {
                                this.tabCurrentIndex = i;
                                break;
                            }
                        }
                    }
                } else {

                }
            }).catch(e => {
                is_loading = false;
            });
        },
        tabClick(index) {
            page = 1;
            is_no_more = false;
            let type = this.typeList[index].id;
            this.tabCurrentIndex = index;
            search = {
                page: page,
                type: type
            };
            this.loadData();
        }
    }
}
